@ccclass('GameScreen')
export class GameScreen extends Component {
    @property(Prefab)
    cardPrefeb: Prefab;
    @property(Node)
    reaveldCard: Node;





    arrayOfCard: any[] = [];


    start() {

    }
    OnClick() {
        this.getDeckOfCard();
    }
    getDeckOfCard() {
        console.log("dhoom dhoom");
        for (let i = 0; i < 1; i++) {
            if (this.cardPrefeb[i].length > 3) {
                return ;
            }
                let card = instantiate(this.cardPrefeb);
                this.reaveldCard.addChild(card);

        }
        }
    //}

    update(deltaTime: number) {

    }
}

